import { Component, Prefab, _decorator, instantiate } from "cc";
import { TransformComponent } from "../components/TransformComponent";
import { SystemType, EntityType } from "../framework/Config";
import { System } from "../framework/System";
import { World } from "../framework/World";
import { Actor } from "../../prefabs/Actor";
const { ccclass, property } = _decorator;

@ccclass('RenderSystem')
export default class RenderSystem extends Component implements System {
    @property(Prefab)
    PrefabArrow: Prefab = null;

    world: World = null;

    type: SystemType = SystemType.Render;

    protected onLoad(): void {
        World.instance.addSystemToCycle(this);
    }

    onUpdate(): void {
        let entities = this.world.newEntitiesInThisCycle;

        for (let i = 0; i < entities.length; i++) {
            let entity = entities[i];

            if (entity.type === EntityType.Actor) {
                let actor = instantiate(this.PrefabArrow).getComponent(Actor);
                actor.entity = entity;
                actor.transform = entity.getComponent(TransformComponent);
                actor.node.parent = this.node;
            }

        }
    }

}